import React from 'react'
import Link from 'gatsby-link'

export default function TopBanner({ style, title }) {
  return (
    <div className="page-top-banner">
      <h2 className="page-heading" style={style}>
        {title}
      </h2>
      <Link to="/" className="page-btn-back">
        {' '}
        <svg className="preview-btn-icon">
          <use xlinkHref="/sprite.svg#icon-keyboard_arrow_left" />
        </svg>Все упражнения
      </Link>
    </div>
  )
}



