import React from 'react'
import Link from 'gatsby-link'
import LazyLoad from 'react-lazyload'

export default function Preview({ post }) {
  return (
    <LazyLoad key={post.id} height={300} offset={100}>
      <div className="preview" style={{ backgroundImage: `url(${post.frontmatter.image})` }}>
        <div className="preview-wrapper">
          <div className="preview-containers">
            <div className="preview-container-1">
              <Link to={post.frontmatter.path} className="preview-title">
                {post.frontmatter.title}
              </Link>
              <div>
                <span className="preview-stats">
                  <Link
                    to={`/bodypart/${post.frontmatter.filterby}`}
                    className="tag tag-bodypart"
                  >
                    {post.frontmatter.bodypart}
                  </Link>
                  <Link
                    to={`/type/${post.frontmatter.filtertype}`}
                    className="tag tag-type"
                  >
                    {post.frontmatter.type}
                  </Link>
                  <Link
                    to={`/inventory/${post.frontmatter.filterinventory}`}
                    className="tag tag-inventory"
                  >
                    {post.frontmatter.inventory}
                  </Link>
                </span>
                <div>
                  <Link to={`/stats/efficiency`} className="tag tag-efficiency">
                  <svg className="tag-icon tag-icon-efficiency">
                    <use xlinkHref="/sprite.svg#icon-check-circle" />
                  </svg>{' '}
                    {post.frontmatter.efficiency}/10
                  </Link>
                  <Link to={`/stats/injury`} className="tag tag-injury">
                  <svg className="tag-icon tag-icon-injury">
                    <use xlinkHref="/sprite.svg#icon-wheelchair" />
                  </svg>{' '}
                    {post.frontmatter.injury}/10
                  </Link>
                </div>
              </div>
              <p className="preview-excerpt">{post.excerpt}</p>
            </div>
            <div className="preview-container-2">
              <Link to={post.frontmatter.path}>
                <img src={post.frontmatter.image} className="preview-image" />
              </Link>
            </div>
          </div>
        </div>
        <Link to={post.frontmatter.path} className="preview-btn">Подробнее
          <svg className="preview-btn-icon">
            <use xlinkHref="/sprite.svg#icon-keyboard_arrow_right" />
          </svg>
        </Link>
      </div>
    </LazyLoad>
  )
}



